<template><section><html><head></head><body><h2>Mars3D Demo</h2>
<p>vue-cesium 使用火星科技的 <a href="http://mars3d.cn/">Mars3D</a> 开发时只需要在引入 VueCesium 时通过配置项 <code v-pre="">mars3dConfig</code> 配置 mars3d 主库和其插件库地址，默认使用 unpkg.com 的 cdn 资源，如需本地或局域网使用，请通过 mars3dConfig.libs 将相关库的资源改为本地或局域网地址即可。非 TS 项目结构请 <a href="https://github.com/zouyaoji/vue-cesium/blob/dev/packages/components/viewer/src/loadUtil.ts#L17">参考</a>。</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">VueCesium</span>, {
  <span class="hljs-attr">mars3dConfig</span>: {
    <span class="hljs-attr">include</span>: <span class="hljs-string">'mars3d'</span>
  }
})

<span class="hljs-comment">// 如果是局部引入，也可以这样:</span>
<span class="hljs-keyword">import</span> { <span class="hljs-title class_">Viewer</span> } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
<span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">Viewer</span>, {
  <span class="hljs-attr">mars3dConfig</span>: {
    <span class="hljs-attr">include</span>: <span class="hljs-string">'mars3d'</span>
  }
})
</code></pre>
<p><code v-pre="">vc-viewer</code> 加载成功会返回 { Cesium, viewer, map }, 通过该 <code v-pre="">map</code> 使用 <a href="http://mars3d.cn/doc">mars3d 教程</a> 和 <a href="http://mars3d.cn/api/">mars3d API</a> 进行相关开发即可， 如下面的例子：</p>
<h2>示例</h2>
<h3>使用 VueCesium 加载火星科技 Mars3D</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :mars3d-config="mars3dConfig" :animation="animation" :timeline="timeline" :fullscreen-button="fullscreenButton" :fullscreen-element="fullscreenElement" @ready="onViewerReady" @leftclick="onLeftClick">
        <vc-navigation></vc-navigation>
        <vc-entity ref="entity" @click="onEntityClick" :position="{lng: 108, lat: 32}" :point="point" :label="label">
          <vc-graphics-billboard ref="billboard" image="https://zouyaoji.top/vue-cesium/favicon.png"></vc-graphics-billboard>
          <vc-graphics-rectangle :coordinates="[130, 20, 80, 25]" material="green"></vc-graphics-rectangle>
        </vc-entity>
        <!-- 天地图注记 -->
        <vc-layer-imagery :sort-order="20">
          <vc-provider-imagery-tianditu map-style="cva_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <!-- 天地图影像 -->
        <vc-layer-imagery :sort-order="10">
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <md-button class="md-raised md-accent" @click="unload">销毁</md-button>
        <md-button class="md-raised md-accent" @click="load">加载</md-button>
        <md-button class="md-raised md-accent" @click="reload">重载</md-button>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"viewerContainer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span>
      <span class="hljs-attr">ref</span>=<span class="hljs-string">"vcViewer"</span>
