        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-billboard</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"billboard"</span> <span class="hljs-attr">image</span>=<span class="hljs-string">"https://zouyaoji.top/vue-cesium/favicon.png"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-billboard</span>&gt;</span>
