      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
      <span class="hljs-comment">&lt;!-- 天地图影像 --&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span> <span class="hljs-attr">:sort-order</span>=<span class="hljs-string">"10"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tianditu</span> <span class="hljs-attr">map-style</span>=<span class="hljs-string">"img_c"</span> <span class="hljs-attr">token</span>=<span class="hljs-string">"436ce7e50d27eede2f2929307e6b33c0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tianditu</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"unload"</span>&gt;</span>销毁<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"load"</span>&gt;</span>加载<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"reload"</span>&gt;</span>重载<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
