<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">animation</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">timeline</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">fullscreenButton</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">fullscreenElement</span>: <span class="hljs-variable language_">document</span>.<span class="hljs-property">body</span>,
        <span class="hljs-attr">point</span>: {
          <span class="hljs-attr">pixelSize</span>: <span class="hljs-number">28</span>,
          <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>
        },
        <span class="hljs-attr">label</span>: {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Hello World'</span>,
          <span class="hljs-attr">pixelOffset</span>: [<span class="hljs-number">0</span>, <span class="hljs-number">150</span>]
        },
        <span class="hljs-attr">billboard</span>: {},
        <span class="hljs-attr">offset</span>: [<span class="hljs-number">10</span>, <span class="hljs-number">25</span>],
        <span class="hljs-attr">otherOpts</span>: {
          <span class="hljs-attr">offset</span>: [<span class="hljs-number">0</span>, <span class="hljs-number">32</span>],
          <span class="hljs-attr">position</span>: <span class="hljs-string">'bottom-right'</span>
        },
        <span class="hljs-attr">mars3dConfig</span>: {
          <span class="hljs-attr">include</span>: <span class="hljs-string">'mars3d'</span>
        }
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">vcViewer</span>.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer }</span>) =&gt;</span> {
